.card-container {
    cursor: pointer;
    height: 400px;
    width: 400px;
    perspective: 800px;
    position: relative;
    margin: auto;
    transition: ease-in;
  }
  
  .card {
    height: 400px;
    width: 400px;
    position: absolute;
    border: 1px solid #300;
    transform-style: preserve-3d;
    transition: all 0.4s ease-in-out;
  }

  .cardUpdate {
    height: 400px;
    width: 400px;
    position: absolute;
    border: 1px solid #300;
    transform-style: preserve-3d;
    transition: all 0.4s ease-in-out;
  }
  
  .card:hover {
    transform: rotateY(180deg);
    transition: all 0.4s ease-in-out;
  }
  
  .card .front {
    height: 400px;
    width: 400px;
    backface-visibility: hidden;
    position: absolute;
    overflow: hidden;
    text-align: center;
    background-color: rgba(255, 251, 251, 1);
  }
  
  .card .back {
    height: 400px;
    width: 400px;
    text-align: center;
    transform: rotateY(180deg);
  }
  
  .question{
    color: #333;
    font-size: 2.3em;
    padding-top: 40%;
  }
  
  .answer{
    color: #333;
    font-size: 2.3em;
    padding-top: 20%;
  }
  
  .source{
      color: rgba(200, 10, 10, 1);
      font-size: 2.3em;
      margin-top: 1em;
  }




.update-form{
  border-radius: 2px;
  width: 100%;
  height: 100%;
  margin: auto;

  align-items: flex-end;
  order: 1;
}

.form-li{
  margin-top: 2em;
  display: block;
}

.input-label{
  display: block;
}

.card-input{
  display: block;
  width: 85%;
  resize: none;
  height: 4.5em;
}

button {
  font-size: 1em;
  border: none;
  height: 1em;
  text-align:center;
  text-decoration:none;
  color: #ecf0f1;
  box-shadow: 0px 6px #2c3e50;
  background: #34495e;
  margin: auto;

}

